import React, { useRef, useEffect, useCallback } from 'react';
import './ProfileCard.css';

const ProfileCard = ({ 
  imagePath, 
  theme,
  name = "Laxman K",
  title = "Software Engineer",
  showUserInfo = true,
  enableTilt = true 
}) => {
  const cardRef = useRef(null);
  const contentRef = useRef(null);

  const handleMouseMove = useCallback((e) => {
    if (!cardRef.current || !enableTilt) return;

    const card = cardRef.current;
    const rect = card.getBoundingClientRect();
    const x = e.clientX - rect.left;
    const y = e.clientY - rect.top;

    const centerX = rect.width / 2;
    const centerY = rect.height / 2;

    const rotateX = ((y - centerY) / centerY) * 10;
    const rotateY = ((centerX - x) / centerX) * -10;

    card.style.transform = `perspective(1000px) rotateX(${rotateX}deg) rotateY(${rotateY}deg) scale3d(1.05, 1.05, 1.05)`;
    contentRef.current.style.transform = `translateZ(50px)`;
  }, [enableTilt]);

  const handleMouseLeave = useCallback(() => {
    if (!cardRef.current || !enableTilt) return;
    
    cardRef.current.style.transform = 'perspective(1000px) rotateX(0) rotateY(0) scale3d(1, 1, 1)';
    contentRef.current.style.transform = 'translateZ(0)';
  }, [enableTilt]);

  useEffect(() => {
    const card = cardRef.current;
    if (!card || !enableTilt) return;

    card.addEventListener('mousemove', handleMouseMove);
    card.addEventListener('mouseleave', handleMouseLeave);

    return () => {
      card.removeEventListener('mousemove', handleMouseMove);
      card.removeEventListener('mouseleave', handleMouseLeave);
    };
  }, [handleMouseMove, handleMouseLeave, enableTilt]);

  return (
    <div className="profile-card-container" ref={cardRef}>
      <div className="profile-card-content" ref={contentRef}>
        <div className="profile-card-image">
          <img
            src={require(`../../assests/images/${imagePath}`)}
            alt="Profile"
            className="profile-pic"
          />
          <div className="profile-card-glow"></div>
          {showUserInfo && (
            <div className="profile-card-info">
              <h2 className="profile-card-name">{name}</h2>
              <p className="profile-card-title">{title}</p>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default ProfileCard;